*{
  margin: 0;
  padding: 0;
}
.App-input{
  text-align: center;
  background-color:lightcoral;
  height: 300px;
  color: navy;
  font-size: 18px;
}
h1{
  text-align: center;
}
.App-input input{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid gray;
  outline: none;
  padding: 10px 5px;
  width: 200px;
  margin-bottom: 20px;


}
button{
  background-color: green;
  border: none;
  color: white;
  font-size: 14px;
  padding: 10px 10px;
  border-radius: 20px;
  
}
nav{
  background-color: chocolate;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
}
nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color: black;
}
nav ul li{
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover{
  transform: scale(1.4);
}

.navActive{
  color: chartreuse;
}
.App-container{
  background-color: blueviolet;
  height: 30px;
}